













































































































import {
  computed, defineComponent, onMounted, onUnmounted, PropType, ref,
} from '@vue/composition-api';
import { ViewportSettings } from '@/app/shared/client/type/settings/viewport-settings';
import {
  FooterTileContent,
  FooterTileDesign,
  FooterTileExternalContent,
} from '@/app/shared/client/type/tile/footer-tile';
import CenterFooterDefaults from '@/app/website/client/defaults/footer/center.json';
import { GeneralDesignSettings } from '@/app/shared/client/type/settings/general-settings';
import { TextSettings } from '@/app/shared/client/type/settings/text-settings';
import { FooterTileLayoutDefaults } from '@/app/shared/client/type/defaults/footer-tile-defaults';
import {
  generalStyles, StyleKeyValue, textStyles, tileStyleSelector,
} from '@/app/website/client/logic/tile-style';
import { isNotNullOrEmpty } from '@/app/shared/client/helper/base';
import { isMadeWithSectionVisible } from '@/app/shared/client/logic/footer-tile';
import { checkIfTileShown } from '@/app/website/client/logic/appearance-behaviour';
import { useWindowScrollSettings } from '@/app/shared/client/logic/scroll-settings';
import { useTileOffsetSettings } from '@/app/shared/client/logic/offset-settings';
import FontLoader from '@/app/website/client/view/component/FontLoader.vue';
import useI18N from '@/app/shared/client/logic/i18n';
import {
  ACCEPT_TRACKING_USER_RESPONSE,
  DECLINE_TRACKING_USER_RESPONSE,
  useTrackingConsent,
} from '@/app/website/client/logic/tracking-consent';

function getTileDesign(userDesign: FooterTileDesign): FooterTileDesign {
  const defaults = CenterFooterDefaults as FooterTileLayoutDefaults;

  const general: GeneralDesignSettings = {
    ...defaults.general,
    ...userDesign.general,
    background: {
      ...defaults.general?.background,
      ...userDesign.general?.background,
      solid: {
        ...defaults.general?.background?.solid,
        ...userDesign.general?.background?.solid,
      },
      gradient: {
        ...defaults.general?.background?.gradient,
        ...userDesign.general?.background?.gradient,
      },
    },
  };

  const copyright: TextSettings = {
    ...(defaults?.copyright as TextSettings),
    ...userDesign.copyright,
  };

  const legalLinks: TextSettings = {
    ...(defaults?.legalLinks as TextSettings),
    ...userDesign.legalLinks,
  };

  const madeWithNotice: TextSettings = {
    ...(defaults?.madeWithNotice as TextSettings),
    ...userDesign.madeWithNotice,
  };

  return {
    layout: userDesign.layout,
    general,
    copyright,
    legalLinks,
    madeWithNotice,
  };
}

function getTileStyles(userDesign: FooterTileDesign): StyleKeyValue[] {
  return [
    ...generalStyles(userDesign.general),
    ...textStyles('copyright', userDesign.copyright),
    ...textStyles('link', userDesign.legalLinks),
    ...textStyles('made-with', userDesign.madeWithNotice),
  ];
}

function composeMadeWithUrl(url: string): string {
  return `${url}?utm_source=instantsite&utm_medium=powered-by-link&utm_campaign=stores`;
}

interface FooterTileProps {
  content: FooterTileContent;
  externalContent: FooterTileExternalContent;
  design: FooterTileDesign;
  viewportSettings: ViewportSettings;
}

const FooterTile = defineComponent({
  components: {
    FontLoader,
  },
  props: {
    content: {
      required: true,
      type: Object as PropType<FooterTileContent>,
    },
    externalContent: {
      required: true,
      type: Object as PropType<FooterTileExternalContent>,
    },
    design: {
      required: true,
      type: Object as PropType<FooterTileDesign>,
    },
    viewportSettings: {
      required: true,
      type: Object as PropType<ViewportSettings>,
    },
  },
  setup(props: FooterTileProps, { attrs, emit, root }) {
    const i18n = useI18N(root.$store);
    const tileRef = ref<HTMLElement>();

    const {
      windowScrollTop,
      subscribeOnWindowScrollChange,
    } = useWindowScrollSettings();

    const {
      tileOffsetTop,
      tileOffsetHeight,
      subscribeOnTileOffsetChange,
    } = useTileOffsetSettings(tileRef);

    const design = computed(() => getTileDesign(props.design));

    const fonts = computed(() => [
      design.value.copyright?.font,
      design.value.madeWithNotice?.font,
    ]);

    const styleSelector = computed(() => {
      const styles = getTileStyles(design.value);
      return tileStyleSelector(`#${attrs.id}`, styles);
    });

    const hasCopyright = computed(() => {
      const isCopyrightVisible = design.value.copyright?.visible;
      const hasCopyrightContent = isNotNullOrEmpty(props.content.copyright);
      return isCopyrightVisible && hasCopyrightContent;
    });

    const isMadeWithNoticeVisible = computed(() => {
      const isVisibleByISSettings = design.value.madeWithNotice?.visible;
      const channelSettings = props.externalContent.channelSettings;
      const isVisibleByChannelSettings = isMadeWithSectionVisible(channelSettings);
      return isVisibleByISSettings && isVisibleByChannelSettings;
    });

    const isTileShowRef = ref(false);
    const isTileShown = computed(() => checkIfTileShown(
      isTileShowRef,
      props.viewportSettings,
      windowScrollTop.value,
      tileOffsetTop.value,
      tileOffsetHeight.value,
    ));

    const madeWithUrl = computed(
      () => composeMadeWithUrl(props.externalContent.channelSettings.madeWithUrl),
    );

    const trackingConsent = useTrackingConsent(root.$store);

    const trackingAllowed = computed({
      get: () => trackingConsent.trackingAllowed.value,
      set: (userResponse) => {
        const trackingUserResponse = (userResponse)
          ? ACCEPT_TRACKING_USER_RESPONSE
          : DECLINE_TRACKING_USER_RESPONSE;
        trackingConsent.setUserResponse(trackingUserResponse);
        if (userResponse === false) {
          window.location.reload();
        }
      },
    });

    const showMadeWithLogoSvg = computed(() => {
      const usePartnerLogoInMadeWithSection = props.externalContent.channelSettings
        .usePartnerLogoInMadeWithSection;
      const logoSvgExists = isNotNullOrEmpty(props.externalContent.channelSettings.madeWithLogoSvg);
      return usePartnerLogoInMadeWithSection && logoSvgExists;
    });

    const showMadeWithLogoUrl = computed(() => {
      const usePartnerLogoInMadeWithSection = props.externalContent.channelSettings
        .usePartnerLogoInMadeWithSection;
      const logoUrlExists = isNotNullOrEmpty(props.externalContent.channelSettings.madeWithLogoUrl);
      return usePartnerLogoInMadeWithSection && logoUrlExists;
    });

    onMounted(() => {
      emit('tile-loaded');
      subscribeOnWindowScrollChange();
      subscribeOnTileOffsetChange();
    });

    onUnmounted(() => {
      emit('tile-unloaded');
    });

    return {
      i18n,
      tileRef,
      fonts,
      styleSelector,
      hasCopyright,
      isMadeWithNoticeVisible,
      isTileShown,
      madeWithUrl,
      trackingConsent,
      trackingAllowed,
      showMadeWithLogoSvg,
      showMadeWithLogoUrl,
    };
  },
});

export default FooterTile;
