import { FooterChannelSettings } from '@/app/shared/client/type/tile/footer-tile';
import { isNotNullOrEmpty } from '@/app/shared/client/helper/base';

export function isMadeWithSectionVisible(channelSettings: FooterChannelSettings): boolean {
  return channelSettings.showFooter
    && (!channelSettings.usePartnerLogoInMadeWithSection
      || isNotNullOrEmpty(channelSettings.logoUrl)
      || isNotNullOrEmpty(channelSettings.madeWithLogoUrl)
      || isNotNullOrEmpty(channelSettings.madeWithLogoSvg));
}

export default {};
